.mc__play {
    background-image: url('../../assets/banner.png');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    padding-bottom: 0rem;
    margin: 0;
    display: flex;
    flex-direction: row;
}

.mc__play-content {
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 5rem 6rem;
    padding-bottom: 50rem;
    background-color: white;
    margin: 3rem 10rem;
    border-radius: 5px;
    text-shadow: 100px;
    font-family: var(--font-family);
    width: 45vw;
}

.mc__play-content p {
    padding: 0.5rem 0rem;
    width: 38vw;
}

.mc__play-content_join div {
    width: 38vw;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;

}

.mc__play-content_join a {
    text-decoration: underline;

}

.mc__play-content_what div {
    height: 3px;
    width: 38vw;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;

}

.mc__play-content_community div {
    height: 3px;
    width: 38vw;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;

}

.mc__play-content_contact div {
    height: 3px;
    width: 38vw;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 1rem;

}

.mc__play-content_button {
    width: 38vw;
}

.mc__play-content_button a {
    padding: 0.7rem 0.7rem;
}
.mc__play-content button {
    color: white;
    background: rgb(161, 161, 161);
    font-family: var(--font-family);
    font-size: 18px;
    text-align: center;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
    font-weight: bold; 
    justify-content: center;
    align-items: right;
    flex: 1;
    display: flex;
    width: 13rem;
    float: right;
}

.mc__play-content_notice {
    background-color: rgb(240, 240, 240);
    padding: 1rem 1rem;
    width: 36vw;
    color: rgb(0, 0, 0);
    border: rgb(175, 175, 175);
    border-style: solid;
    border-width: 2px;
    margin: 2rem 1rem;

}
.mc__play-content_notice h2 {
    font-size: 20px;
    padding: 0rem 1rem;

}

.mc__play-content_notice p {
    font-size: 15px;
    padding: 0.5rem 1rem;
    padding-right: 5rem;
}

@media screen and (max-width: 1600px) {
    .mc__play-content {
        margin: 3rem 5rem;
        padding: 4rem 5rem;
    }
}

@media screen and (max-width: 1000px) {
    .mc__play-content {
        padding: 3rem 4rem;
    }
    
    .mc__play-content p {
        width: auto;
    }
    .mc__play-content_button {
        width: auto;
    }
    .mc__play-content_join div {
        width: auto;
    }
    .mc__play-content_what div {
        width: auto;
    }
    .mc__play-content_community div {
        width: auto;
    }
    .mc__play-content_contact div {
        width: auto;
    }
    .mc__play-content_notice {

        width: auto;
    
    }
    .mc__play-content_notice p {
        padding: 0.5rem 1rem;
    }
}

@media screen and (max-width: 700px) {
    .mc__play-content {
        margin: 2rem 2rem;
        padding: 3rem 3rem;
    }
}


@media screen and (max-width: 414px) {
    .mc__play-content {
        margin: 1.5rem 1.5rem;
        padding: 3rem 3rem;
    
    }
    .mc__play-content_notice {
        margin: 1rem 0;
        margin-bottom: 2rem;
    
    }
    .mc__play-content_button {
        padding: 1rem 0;
    }
}